* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  background: #fafafa;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  z-index: 1000;
  background: #4a90e2;
  justify-content: space-between;
  align-items: center;
  height: 4rem;
  padding: 1rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

header h1 {
  margin: 0;
  font-size: 1.5rem;
}

#add-figure-btn {
  cursor: pointer;
  padding: .5rem 1rem;
}

.header-controls {
  align-items: center;
  gap: .5rem;
  display: flex;
}

#layout-select {
  padding: .4rem;
}

main {
  gap: 1rem;
  margin-top: 4rem;
  padding: 1rem;
  display: grid;
}

.metric-list {
  overflow-y: auto;
}

.layout-auto {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
}

.layout-single {
  grid-template-columns: 1fr;
}

.layout-2x2 {
  grid-template-columns: repeat(2, 1fr);
}

.layout-3x3 {
  grid-template-columns: repeat(3, 1fr);
}

.figure {
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  flex-direction: row;
  max-height: calc(90vh - 4rem);
  padding: .5rem;
  display: flex;
  position: relative;
  overflow: auto;
}

.layout-2x2 .figure, .layout-3x3 .figure, .layout-auto .figure, .layout-single .figure {
  min-height: calc(80vh - 4rem);
}

.figure canvas {
  object-fit: contain;
  flex: 1;
  min-height: 0;
  width: 100% !important;
}

.right-container {
  flex-direction: column;
  flex: 1;
  display: flex;
}

.left-container {
  flex-direction: column;
  flex: none;
  height: 100%;
  margin-right: .5rem;
  display: flex;
}

.metric-list {
  flex: 1;
  overflow-y: auto;
}

.figure .controls {
  gap: .5rem;
  margin-top: .5rem;
  display: flex;
}

.modal {
  background: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal.hidden {
  display: none;
}

.modal-content {
  background: #fff;
  border-radius: 4px;
  max-width: 90%;
  max-height: 80%;
  padding: 1rem;
  overflow: auto;
}

.close-btn {
  cursor: pointer;
  font-size: 1.5rem;
  position: absolute;
  top: .5rem;
  right: .8rem;
}

@media (width <= 768px) {
  header {
    flex-direction: column;
    align-items: flex-start;
    gap: .5rem;
  }
}
